"use client";

interface Props {
  resetLink: string;
}

export default function ResetPassword({ resetLink }: Props) {
  return (
    <section>
      <div style={{ maxWidth: "600px", margin: "0 auto", padding: "20px" }}>
        {/* Header */}
        <div
          style={{
            textAlign: "center",
            padding: "30px 20px",
            background:
              "linear-gradient(to right, #312e81, #3730a3, #4338ca, #6366f1, #818cf8)",
            borderRadius: "8px 8px 0 0",
          }}
        >
          <h1
            style={{
              margin: 0,
              fontSize: "32px",
              fontWeight: 600,
              background:
                "linear-gradient(to right, #1f2937, #4f46e5, #6366f1, #4f46e5, #1f2937)",
              WebkitBackgroundClip: "text",
              opacity: 0.7,
            }}
          >
            Assetly
          </h1>

          <p   style={{
              margin: 0,
              fontSize: "32px",
              fontWeight: 600,
              background:
                "linear-gradient(to right, #1f2937, #4f46e5, #6366f1, #4f46e5, #1f2937)",
              WebkitBackgroundClip: "text",
              opacity: 0.7,
            }}
          >
            Resetea tu contraseña
          </p>
        </div>

        {/* Body */}
        <div
          style={{
            padding: "30px",
            backgroundColor: "#111827",
            color: "#ffffff",
          }}
        >
          <p>Hola,</p>
          <p>
            Hemos recibido una solicitud para restablecer tu contraseña. Por
            favor, haz clic en el botón a continuación para proceder.
          </p>
          <div style={{ textAlign: "center", margin: "30px 0" }}>
            <a
              href={resetLink}
              style={{
                display: "inline-block",
                padding: "12px 24px",
                background: "linear-gradient(to top, #4f46e5, #6366f1)",
                color: "#ffffff",
                textDecoration: "none",
                borderRadius: "5px",
                fontWeight: 500,
                transition: "background 0.3s ease",
              }}
            >
              Resetear Contraseña →
            </a>
          </div>
          <p>Este enlace expirará en 30 minutos.</p>
          <p>Si no solicitaste este cambio, ignora este correo.</p>
        </div>

        {/* Footer */}
        <div
          style={{
            padding: "20px",
            textAlign: "center",
            backgroundColor: "#1f2937",
            color: "#ffffff",
          }}
        >
          <p>Tu forma Segura y Eficiente de Gestionar tus Activos</p>
          <p>© 2025 Assetly. Todos los derechos reservados.</p>
        </div>
      </div>
    </section>
  );
}
